import * as React from 'react';
import { User } from '@common/User';
import { connect } from 'react-redux';

import { staticVals } from '@store/static';

const ROOT_CLASS = 'trader-card';

const TraderCard = props => {
  const { productId, edit, } = props;
  
  let rating = props.rating;
  let value = rating.value; 

  let score = (value * 100).toFixed(2);
  score = score > 100 ? 100 : score;
  score = score < -100 ? -100 : score;

  const buildRootClass = () => {
    return `${ROOT_CLASS}`;
  }

  return (
    <div className={`${ROOT_CLASS}__parentCardForButtons`}>
      <div className={buildRootClass()} onClick={props.onClick ? props.onClick : () => {}}>
        <div className={`${ROOT_CLASS}__header`}>
          <div className={`${ROOT_CLASS}__user-block`}>
            <User
              nameProduct={props.name}
              rating={rating}
              block="traderCard"
              stock={props.stock}
            />
          </div>
        </div>
        <div className={`${ROOT_CLASS}__onetag`}>
          <div className={`${ROOT_CLASS}__onetag__key`}>Trader:</div>
          <div className={`${ROOT_CLASS}__onetag__desc`}>{`${props.trader.firstName} ${props.trader.lastName}`}</div>
        </div>
        <div className={`${ROOT_CLASS}__moretag`}>
          {
            staticVals[props.name] && (
              <div className={`${ROOT_CLASS}__moretag_item`}>
                <div className={`${ROOT_CLASS}__moretag_item__key`}>Market:</div>
                <div className={`${ROOT_CLASS}__moretag_item__desc`}>{staticVals[props.name].Market}</div>          
              </div>
            )
          }
          <div className={`${ROOT_CLASS}__moretag_item`}>
            <div className={`${ROOT_CLASS}__moretag_item__key`}>Base asset:</div>
            <div className={`${ROOT_CLASS}__moretag_item__desc`}>{props.baseAsset}</div>          
          </div>
          {
            staticVals[props.name] && (
              <div className={`${ROOT_CLASS}__moretag_item`}>
                <div className={`${ROOT_CLASS}__moretag_item__key`}>Type:</div>
                <div className={`${ROOT_CLASS}__moretag_item__desc`}>{staticVals[props.name].Type}</div>          
              </div>
            )
          }
        </div>
        <div className={`${ROOT_CLASS}__onetagmargin`}>
          <div className={`${ROOT_CLASS}__onetagmargin__key`}>Description</div>
          <div className={`${ROOT_CLASS}__onetagmargin__desc`}>{props.description}</div>
        </div>
      </div>
      {
        edit && (
          <div className={`${ROOT_CLASS}__onetagmargin ${ROOT_CLASS}__adminEvents`}>
            <div className={`${ROOT_CLASS}__onetagmargin__desc clickAdmin edit`} onClick={props.clickEdit.bind(this, {
              productId,
            })}>edit</div>
            <div className={`${ROOT_CLASS}__onetagmargin__desc clickAdmin delete`} onClick={props.clickDelete.bind(this, {
              productId,
            })}>delete</div>
          </div>
        )
      }
     
    </div>
  );
};

const mapStateToProps = state => {
  return {
    products: state.marketplace.products
  };
}

const connectedContainer =
  connect(mapStateToProps)(TraderCard);

export {connectedContainer as TraderCard};


